<template>
  <TitleAnimated
    timeoutMin=10
    timeoutMax=15
  />
  <div class="area-main">
  </div>
  <ControlsPanelBase>
    <template #controls-first>
      <div 
        class="more relative"
        :class="{ active: showProfiles }"
      >
        <div
          v-if="!this.currentProfile && profilesList.length < 3"
          :class="`more-${(profilesList.length+1)}`"
          class="tile-more tile-blue tile-round svg-plus"
          @click="this.$router.push({ name: 'create-profile' })"
        />
        <div
          v-for="(profile, index) in getProfilesList"
          :key="index"
          :class="[`more-${(index+1)}`, `svg-avatar-${profile.avatarId}`]"
          class="tile-more tile-blue tile-round"
          @click="this.setProfileHandler(profile)"
        />
        <div
          v-if="!this.currentProfile"
          class="tile-blue tile-round svg-user relative"
          @click="showProfiles = !showProfiles"
        />
        <div
          v-if="this.currentProfile"
          class="tile-more more-2 bg-blue tile-round svg-graduation"
          @click="this.$router.push({ name: 'profile' })"
        />
        <div
          v-if="this.currentProfile"
          class="tile-more more-1 bg-blue tile-round svg-back"
          @click="this.setProfileHandler()"
        />
        <div
          v-if="this.currentProfile"
          :class="`svg-avatar-${this.currentProfile.avatarId}`"
          class="bg-white tile-round relative"
          @click="showProfiles = !showProfiles"
        />
      </div>
    </template>
    <template #controls-center>
      <div
        class="tile-blue tile-game"
        @click="this.$router.push({ name: 'games-list'})"
      >
        ГРАТИ
      </div>
    </template>
    <template #controls-last>
      <div 
        class="more relative"
        :class="{ active: showMore }"
      >
        <div
          class="tile-more tile-blue tile-round svg-made-in-ukr-color"
          :class="IS_CAN_SHARE ? 'more-2' : 'more-1'"
          @click="this.$router.push({ name: 'about'})"
        />
        <div
          class="tile-more more-1 tile-blue tile-round svg-baloon"
          @click="this.shareHandler()"
          v-if="IS_CAN_SHARE"
        />
        <div
          class="tile-blue tile-round svg-dots3 more-button relative"
          :class="{ active: showMore }"
          @click="showMore = !showMore"
        />
      </div>
    </template>
  </ControlsPanelBase>
  <div 
    class="app_build_id"
    :class="{ visible: isBuildVisible }"
    @click="isBuildVisible = !isBuildVisible"
  >
    Biuld: {{APP_BUILD_ID}} | Plaform: {{PLATFORM}} | Installed: {{INSTALLED}} | Can share: {{IS_CAN_SHARE ? 'yes' : 'no'}}
  </div>
</template>

<script>
import TitleAnimated from '@/components/TitleAnimated';
import DomMixin from '@/mixins/domMixin';
import UtilsMixin from '@/mixins/utilsMixin';
import ProfileMixin from '@/mixins/profileMixin';
import ControlsPanelBase from '@/components/ControlsPanelBase';
import { PLATFORM, INSTALLED, IS_CAN_SHARE, EMOJI } from '@/utils'
export default {
    mixins: [
      DomMixin,
      UtilsMixin,
      ProfileMixin
    ],
    created() {
      this.domSetDocumentBackgroundUrl(require('/public/img/bg-start-1080p.png'));
    },
    beforeMount() {
      this.loadProfilesList()
    },
    components: {
      TitleAnimated,
      ControlsPanelBase,
    },
    methods: {
      loadProfilesList() {
        this.profilesList = this.loadProfiles().slice(0, 3)
        this.currentProfile = this.getProfile(this.getCurrentProfile())
      },
      setProfileHandler(profile = null) {
        this.showProfiles = false;
        this.currentProfile = profile;
        this.setCurrentProfile(profile?.id);
      },
    },
    computed: {
      getProfilesList() {
        return this.currentProfile ? [] : this.profilesList
      },
    },
    data() {
      return {
        PLATFORM,
        INSTALLED,
        IS_CAN_SHARE,
        EMOJI,
        // eslint-disable-next-line
        APP_BUILD_ID: APP_BUILD_ID,
        isBuildVisible: false,
        showMore: false,
        showProfiles: false,
        profilesList: [],
        currentProfile: null,
      }
    }
}
</script>

