  position: relative;
}

.more .tile-more {
  position: absolute;
  opacity: 0;
  transition: all .5s cubic-bezier(0, .5, .5 ,1);
  top: 0;
  // z-index: -1;
}

.more.active .tile-more {
  opacity: 1;
  &.more-1 { top: -10vw; }
  &.more-2 { top: -20vw; }
  &.more-3 { top: -30vw; }
}

.tile-game {
  padding: 0 2vw;
}

.svg-dots3::before {
  transition: rotate .5s ease-in-out;
}

.active.svg-dots3::before {
  rotate: 450deg;
}

