// }

.app_build_id {
  position: absolute;
  bottom: 0;
  left: 0;
  color: black;
  background-color: white;
  font-size: 1.2vw;
  padding: 2px;
  opacity: 0;
}

.app_build_id.visible {
  opacity: .8;
}
</style>

