export default {
    methods: {
        domSetDocumentBackgroundUrl(img) {
            document.documentElement.style.backgroundImage = `url(${img})`;
        },
        domGetWindowOrientation() {
            return parseWindowOrientation(
                (screen.orientation || {}).type || screen.mozOrientation || screen.msOrientation
            );
        },
        domOnWindowOrientationChange(callback) {
            screen.orientation.addEventListener("change", e => {
                callback(parseWindowOrientation(e.target.type));
            });
        },
    }
}

const parseWindowOrientation = (orientation) => {
    if (orientation) {
        orientation = orientation.split('-')[0];
    }
    return orientation;
}