export default {
    methods: {
        async shareHandler() {
            try {
                await navigator.share({
                 title: 'Навчайко',
                 url: 'https://navchaiko.app',
                 text: 'Навчайся граючись'
                })
             } 
             // eslint-disable-next-line
             catch (e) {}
        },
    }
}