<template>
  <router-view
    v-if="isOrientationCorrect"
  />
  <VerticalHome
    v-else
  />
</template>

<script>
import VerticalHome from '@/views/VerticalView'
import { loadImages, INSTALLED } from '@/utils'
import domMixin from '@/mixins/domMixin'
export default {
    mixins: [domMixin],
    components: {VerticalHome},
    data() {
        return {
            orientation: this.domGetWindowOrientation()
        };
    },
    computed: {
        isOrientationCorrect() {
            return this.orientation !== "portrait" || INSTALLED;
        }
    },
    created() {
        loadImages([
            require("/public/img/bg-start-1080p.png"),
            require("/public/img/bg-empty1-1080p.png"),
            require("/public/img/bg-game1-1080p.png"),
        ]);
        this.domOnWindowOrientationChange(orientation => {
            this.orientation = orientation;
        });
    }
}
</script>
