export function getRandomValueList(string) {
    let resultUnsorted = arrayFrom(string);
    if (arrayUnique(resultUnsorted).length < 2) {
        return resultUnsorted;
    }
    let resultSorted = [...resultUnsorted];
    while (resultSorted.join() === resultUnsorted.join()) {
        resultSorted.sort(() => 0.5 - Math.random());
    }
    return resultSorted;
}

export function arrayFrom(value) {
    return Array.isArray(value) ? value : Array.from(value);
}

export function arrayUnique(array) {
    return array.filter((v, i, a) => a.indexOf(v) === i);
}

export function loadImages(imgList) {
    imgList.map(v => {
        const img = new Image();
        img.src = v;
    });
}

