export function getDateStamp(date = null) {
    const now = date || new Date();
    return new Date(Date.UTC(now.getFullYear(), now.getMonth(), now.getDate()))
        .valueOf()/(1000*60*60*24);
}

export function getDateFromStamp(stamp) {
    return new Date(stamp*1000*60*60*24)
}

const UA = navigator.userAgent;
const IOS = UA.match(/iPhone|iPad|iPod/);
const ANDROID = UA.match(/Android/);
export const PLATFORM = IOS ? 'ios' : ANDROID ? 'android' : 'unknown';

const standalone = window.matchMedia('(display-mode: standalone)').matches
    || window.matchMedia('(display-mode: fullscreen)').matches;
export const INSTALLED = !!(standalone || (IOS && !UA.match(/Safari/)));
export const IS_CAN_SHARE = navigator.share;