import Storage from '@/storage';
import { getDateStamp } from '@/utils'; 

export default {
    methods: {
        getProfiles() {
            return Storage.getItemObject('profilesList', [])
        },
        getProfile(profileId) {
            return Storage.getItemObject(`profile-${profileId}`)
        },
        setProfile(profile) {
            Storage.setItem(`profile-${profile.id}`, profile)
            return profile
        },
        loadProfiles() {
            return this.getProfiles().map(id => this.getProfile(id)).filter(v => v?.id && v?.avatarId)
        },
        createProfile(avatarId) {
            let profiles = this.getProfiles()
            let profileId
            for (profileId = 1; profileId <= profiles.length; profileId++) {
                if (!profiles.includes(profileId)) break
            }

            const profile = { id: profileId, avatarId }
            profiles.push(profileId)

            Storage.setItem('profilesList', profiles)
            return this.setProfile(profile)
        },
        deleteProfile(profileId) {
            Storage.removeItem(`profile-${profileId}`)
            Storage.setItem('profilesList', this.getProfiles().filter(v => v !== profileId))
        },
        getCurrentProfile() {
            return Storage.getItem('currentProfile')
        },
        setCurrentProfile(profileId) {
            if (profileId) {
                Storage.setItem('currentProfile', profileId)
            } else {
                this.removeCurrentProfile()
            }
        },
        removeCurrentProfile() {
            Storage.removeItem('currentProfile')
        },
        addProfilePoints(profileId, gameId, modeId, pointWin, pointExtra = 0, pointTimer = 0) {
            const profile = this.getProfile(profileId)
            if (profile && (pointWin > 0 || pointExtra > 0 || pointTimer > 0)) {
                const points = profile.points || {}
                const pointsItemKey = `${getDateStamp()}_${gameId}-${modeId}`
                const pointsItem = points[pointsItemKey] || [0,0]
                pointsItem[0] = (pointsItem[0] ?? 0) + pointWin
                pointsItem[1] = (pointsItem[1] ?? 0) + pointExtra
                if (pointTimer > 0) {
                    pointsItem[2] = Math.max(pointsItem[2] ?? 0, pointTimer)
                }
                points[pointsItemKey] = pointsItem
                profile.points = points
                this.setProfile(profile)
            }
            return profile
        },
        getProfilePoints(profileId, gameId, modeId) {
            const profile = this.getProfile(profileId)
            if (profile) {
                const points = profile.points || {}
                const pointsItemKey = `${getDateStamp()}_${gameId}-${modeId}`
                const pointsItem = points[pointsItemKey] || []
                return {
                    win: pointsItem[0] ?? 0,
                    extra: pointsItem[1] ?? 0,
                    timer: pointsItem[2] ?? 0,
                }
            }
            return null
        }
    },
}