<template>
    <div class="title-animated">
        <span
            v-for="(item, index) in titleList"
            :key="index"
            :class="{ active: item.active }"
            @click="() => { item.active = true; this.timer = 0 }"
            @animationend="item.active = false"
        >
            {{item.letter}}
        </span>
    </div>
</template>

<script>
import { arrayFrom } from '@/utils';
export default {
  props: [ 'timeoutMin', 'timeoutMax' ],
  data() {
    return {
      titleList: [],
      timer: 0,
      timeout: 0, 
      setTimeout: null,
      titleKeys: [],
    }
  },
  created() {
    this.titleList = arrayFrom('НАВЧАЙКО')
      .map(v => { return { letter: v, active: false }})
    if (this.timeoutMin && this.timeoutMax) {
      this.tickTimer()
    }
  },
  methods: {
    tickTimer() {
      this.setTimeout = setTimeout(() => {
        this.timer += 1
        this.tickTimer()
      }, 1000)

      if (this.timer === this.timeout) {
        if (this.timeout > 0 && !this.titleList.find(v => v.active === true)) {
          this.runRandomAnimation()
        }
        this.timer = 0
        this.timeout = +this.timeoutMin + Math.floor(Math.random() * (this.timeoutMax - this.timeoutMin))
      }
    },
    runRandomAnimation() {
      if (this.titleKeys.length === 0) {
        this.titleKeys = [...this.titleList.keys()].sort(() => 0.5 - Math.random())
      }
      this.titleList[this.titleKeys.pop()].active = true
    },
  },
  beforeUnmount() {
    if (this.setTimeout) {
      clearTimeout(this.setTimeout);
    }
  }
}
</script>

<style scoped>
