  }

  15%, 35%, 55% {
    transform: scale(0.7, 1.5) translate(0, -20vh);
    visibility: visible;
  }

  16%, 36%, 56% {
    transform: scale(0.7, 1.5) translate(0, -20vh);
    visibility: hidden;
  }
  
  17%, 37%, 57% {
    transform: scale(0.7, 1.5) translate(0, 100vh);
    visibility: hidden;
  }
  
  95% {
    transform: scale(1.5, 0.5) translate(0, -20vh);
    visibility: visible;
  }

  100% {
    transform: scale(1) translate(0, 0);
    visibility: visible;
  }
}

.title-animated span:nth-child(3).active {
  animation: falling 2s ease-out;
  transform-origin: bottom center;
}

@keyframes falling {
  12% {
    transform: rotateX(240deg);
  }
  
  24% {
    transform: rotateX(150deg);
  }
  
  36% {
    transform: rotateX(200deg);
  }
  
  48% {
    transform: rotateX(175deg);
  }
  
