const Storage = {
    getItem: function(key) {
        return localStorage.getItem(key);
    },
    getItemObject: function(key, def = null) {
        try {
            return JSON.parse(this.getItem(key)) ?? def;
        } catch(e) {
            this.removeItem(key);
            return def;
        }  
    },
    getItemValue: function(key, id, def = null) {
        return this.getItemObject(key, {})[id] ?? def;
    },
    setItem: function(key, value) {
        if (typeof value !== 'string') {
            value = JSON.stringify(value);
        }
        localStorage.setItem(key, value);
    },
    setItemValue: function(key, id, value) {
        this.setItem(key, {...this.getItemObject(key), ...{[id]: value}});
    },
    removeItem: function(key) {
        localStorage.removeItem(key);
    },
}

export default Storage;